<template>
  <div>
    <div id="mdiv"  class="message_div  container" >
      <p id="p1">I am a Frontend Developer</p>
      <p id="p2">I can help you make your website</p>
    </div>
  </div>
  
</template>

<script>
export default {
  name:'Message',
  mounted(){
    const observer= new IntersectionObserver(entrys=>{
        entrys.forEach((enty)=>{
            if(enty.isIntersecting){
                enty.target.classList.add('message_div_a')
                
            }
        })
    },{threshold:0.4})
    const Seel = document.querySelectorAll('.message_div')
    Seel.forEach((o)=>{observer.observe(o)})
  },

}
</script>

<style scoped>
@keyframes anim{
  from{opacity: 0;}
  to{opacity: 1;}
}
.message_div{
    
    color: #248cdb;
    padding: 3%;
    padding-left: 10%;
    padding-right: 10%;
    opacity: 0;
    margin-top: 10%;
    margin-bottom: 30%;
    height: fit-content;
    background:linear-gradient(to left ,#93b6ce,#e2eef8,#f7fcff,#f7fcff);
    box-shadow: -15px 0px 30px rgb(78, 78, 78);
}
.message_div_a{
  animation-name: anim;
  animation-duration: 2s;
  opacity: 1;
}
p{
  font-size:300% ;
  background:linear-gradient(to left top ,#50a3e2,#2f82c2,#062b47);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
</style>