<template>
  <div class='topping_div shadow-lg'>
    <br>
    <br>
    <p id="mainp" @mouseover="mouse_over_p('mainp')" @mouseleave="mouse_le_p('mainp')" class="topping_p p1">Hi, I'm Oday </p>
    <br>
    <p  id="posp" @mouseover="mouse_over_p('posp')" @mouseleave="mouse_le_p('posp')" class="pos_p p1">Libya, Sabha </p>
    <img @click="shake('toppingimage')" class="topping_image" id="toppingimage" src="../assets/profilee.png">
    <br>
    <br>
    <p class="job_p" id="jopp" @mouseover="mouse_over_p('jopp')"  @mouseleave="mouse_le_p('jopp')">| Frontend Developer |</p>
  </div>
</template>

<script>
import sound from '../assets/image_pressed_s.wav'

export default {
    name:'Top',

    methods:{
    shake(q){
      const ele = document.getElementById(q)
      ele.animate([{transform:'translateX(10px)'},{transform:'translateX(-10px)'},{transform:'translateX(0px)'}],{duration:200 ,iterations:3 })
      var soun = new Audio(sound)
      soun.play()
    },
    mouse_over_p(q){
      const ele = document.getElementById(q)
      ele.style.animationName='p_onm'
      ele.style.animationDuration='500ms'
      ele.style.textShadow='0px 0px 15px  rgb(0, 157, 248)'
      ele.style.transform='scale(1.2,1.2)'
    },
    mouse_le_p(q){
      const ele = document.getElementById(q)
      ele.style.animationName='p_onl'
      ele.style.animationDuration='500ms'
      ele.style.textShadow='none'
      ele.style.transform='scale(1,1)'
      
    },
    }
}
</script>

<style>
@keyframes p_onl{
    from{text-shadow: 0px 0px 15px rgb(0, 157, 248);transform:scale(1.2,1.2)}
    to {text-shadow:none;transform:scale(1,1)}
}
@keyframes p_onm{
    from {text-shadow:none;transform:scale(1,1)}
    to{text-shadow: 0px 0px 15px  rgb(0, 157, 248);transform:scale(1.2,1.2)}
}
@keyframes topping_p_a{
    from{font-size: 0%}
    to{font-size: 300%;}
}
@keyframes topping_image_a{
    from{opacity:0;font-size: 0%}
    to{opacity:1;width:30%}
}
.topping_div{
  width: 100%;
  background-image: url('../assets/background.jpg');
  height: 400px;
}
.topping_p{
  color:white;
  font-size:300%;
  width: fit-content;
  animation-name:topping_p_a;
  animation-duration: 1.5s;
  margin: auto;

}
.pos_p{
  color:white;
  font-size: large;
  width: fit-content;
  margin: auto;
}
.topping_image{
  margin-top: 3%;
  border-radius: 50%;
  background-color: #a2c3dd;
  width:30%;
  animation-name:topping_image_a;
  animation-duration: 1.5s;
  box-shadow: -7px 0px 20px black;
}

.job_p{
  margin-top: 1%;
  font-style: italic;
  color: black;
  text-shadow: -1px 0px 0px #ffffff,0px -1px 0px #046bb1;
  margin:auto;
  width: fit-content;
}
.p1{
  background:linear-gradient(to left top ,#87bee9,#c0e4ff,#ffffff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

}
</style>